.sidebar {
  position: absolute;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100vw;
  z-index: 2;
}

.sidebar.open {
  width: 317px;
  background-color: white;
  box-shadow: 0 10px 40px 0 #1D212621;

}

.background {
  background-color: #000;
  opacity: .3;
  width: 100vw;
  height: 100vh;
}

.background::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3); /* Прозрачный черный цвет фона */
  z-index: -1; /* Чтобы псевдоэлемент находился под другими элементами */
}

.nav {
  padding-top: 63px;
  padding-left: 70px;
  display: flex;
  flex-direction: column;
  gap: 45px;
}

.nav a {
  font-size: 22px;
  font-weight: 600;
  text-decoration: none;
  color: #000;
}

.nav a:hover {
  color: #58ACFF;
}

.nav a.active {
  color: #0059B2;
  text-decoration: underline;
}
.close {
  cursor: pointer;
  background-color: transparent;
  border: none;
  position: absolute;
  top: 16px;
  right: 24px;
}

.close img {
}
